import { Component, ComponentFactoryResolver, ViewChild, ViewContainerRef } from '@angular/core';
import { IonicPage, Events, MenuController } from 'ionic-angular';
import { DragulaService } from 'ng2-dragula';
import { SocketServiceProvider } from '../../providers/socket-service/socket-service';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { tabDataInterface, globalConfigInterface } from '../../utilities/interface';
import { addComponentFromDump, recreateComponenFromDump, addGlobalCSSFromDump } from '../render-components-dump';
import { Subject } from 'rxjs';


@IonicPage()
@Component({
  selector: 'page-designer',
  templateUrl: 'designer.html',
})
export class DesignerPage {

  @ViewChild('container', { read: ViewContainerRef }) container: ViewContainerRef;
  mode: string = 'Editing';
  isViewCodemirror: boolean = false;
  tab: Subject<tabDataInterface> = new Subject();
  containerHeight: string = '100%';

  constructor(public ds: DragulaService, public events: Events, public resolver: ComponentFactoryResolver,
    public ssp: SocketServiceProvider, public gsp: GlobalServiceProvider,
    public menuCtrl: MenuController) {
    this.events.subscribe('page-menu : page-designer && page-code-mirror : add-component', (component) => {
      addComponentFromDump(component, this.container, this.resolver);
    });

    this.events.subscribe('global-service : page-designer && page-code-mirror : recreate-designer-components', () => {
      this.reloadComponents();
    });

    this.events.subscribe('page-menu : page-designer : mode', (mode: string) => {
      this.mode = mode;
    });

    this.events.subscribe('editor : page-designer : update-page', () => {
      console.log('updating');
      addGlobalCSSFromDump(this.gsp.global_config.css.getValue());
    });

    this.events.subscribe('component-activity-bar : page-designer : show-designer', () => {
      this.isViewCodemirror = false;
    });

    this.events.subscribe('component-console : page-designer : update-container-height', () => {
      this.containerHeight = `${document.querySelector('page-designer').clientHeight + this.gsp.consoleData.top - 86}px`;
      
    });
  }

  ionViewDidLoad() {
    // this.onReloading();
    addGlobalCSSFromDump(this.gsp.global_config.css.getValue());
    this.containerHeight = `${document.querySelector('page-designer').clientHeight + this.gsp.consoleData.top - 86}px`;
  }

  ionViewDidLeave() {
    this.events.unsubscribe('page-menu : page-designer : add-component');
    this.events.unsubscribe('page-menu : page-designer : mode');
    this.events.unsubscribe('editor : page-designer : update-page');
  }

  updateComponents() {
    try {
      let globalConfig: globalConfigInterface['js'] = JSON.parse(this.gsp.global_config.json.getValue());
      // let findArray: Array<any> = [];
      globalConfig.rendered_views.map((renderedComponent) => {
        this.gsp.designerComponents.forEach((val, index, array) => {
          // let data: componentDataInterface = val.data;
          if (renderedComponent.id == val.data.config.id) {
            array[index].data.config.variable = renderedComponent.variable;
            renderedComponent.name != undefined ? array[index].data.config.name = renderedComponent.name : null;
          }
        });
      });
      console.log(this.gsp.designerComponents);
    } catch (err) {
      console.log(err);
    }
  }

  closeTab(tab: tabDataInterface) {
    this.tab.next(tab);
    if (tab.other == 'globalConfigDefaultSettings') {
      this.events.publish('page-designer : global-service : update-settings');
    }
    if (this.gsp.tabsArray.length == 0) {
      this.isViewCodemirror = false;
    }
  }

  openTab(tab: tabDataInterface) {
    this.gsp.currentTab = tab;
    /*
      Bug here : need to send event twice to trigger tab change
    */
    if (this.isViewCodemirror) {
      this.events.publish('page-designer : page-code-mirror : open-tab');
      setTimeout(() => {
        this.events.publish('page-designer : page-code-mirror : open-tab');
      }, 50);
    } else {
      this.isViewCodemirror = true;
      setTimeout(() => {
        this.events.publish('page-designer : page-code-mirror : open-tab');
      }, 50);
    }
  }

  onReloading() {
    this.updateComponents();
    addGlobalCSSFromDump(this.gsp.global_config.css.getValue());
    this.gsp.designerComponents.length > 0 ? this.reloadComponents() : null;
  }

  reloadComponents() {
    this.container.clear();
    this.gsp.designerComponents.map((_component) => {
      recreateComponenFromDump(_component, this.container, this.resolver);
    });
  }
}



